<template>
  <div class="example-tabs">
    <h3></h3>
    <component-tabs :tabs="tabs" :active-tab-index.sync="activeTabIndex"></component-tabs>

    <code class="example">
       Vue ':is'  .. tab  <br>
       : {{activeTabIndex | json}}
    </code>

    <example-code>
      <textarea>
      <component-tabs :tabs="tabs" :active-tab-index.sync="activeTabIndex"></component-tabs>

      data () {
        return {
          activeTabIndex: 1,
          tabs: ['', '', '', '', '']
        }
      },
      events: {
        'component-tabs-changed' (index) {
          console.log(index)
        }
      }
      </textarea>
    </example-code>
  </div>
</template>

<script>
import tabs from 'src/tabs/tabs'
import code from '../example-code'

export default {
  name: 'example-tabs',
  data () {
    return {
      activeTabIndex: 1,
      tabs: ['', '', '', '', '']
    }
  },
  components: {
    'component-tabs': tabs,
    'example-code': code
  }
}
</script>

