<template>
  <div class="example-dialog">
    <h3></h3>
    <button @click="show =true">dialog</button>
    <component-dialog v-if="show" :show.sync="show" class="component-dialog">
      <div slot="content">
        <h3> Dialog  ?</h3>
        <p> Dialog </p>
      </div>
    </component-dialog>

    <example-code>
      <textarea>
      <button @click="show = true">dialog</button>

      <component-dialog v-if="show" :show.sync="show" class="component-dialog">
        <div slot="content">
          <h5>Dialog</h5>
        </div>
      </component-dialog>
      </textarea>
    </example-code>
  </div>
</template>

<script>
import dialog from 'src/dialog/dialog'
import code from '../example-code'

export default {
  name: 'example-dialog',
  data () {
    return {
      show: false
    }
  },
  components: {
    'component-dialog': dialog,
    'example-code': code
  }
}
</script>

<style lang="scss">
.component-dialog {
  .dialog-container {
    width: 360px;
  }
}
</style>



